import './Form.css';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import { formatDateForInput } from '../helpers/dateStuff.js'

const Form = () => {
  const [date, setDate] = useState( formatDateForInput() );
  const [gas, setGas] = useState('');
  const [miles, setMiles] = useState('');

  const calcMpg = () => {
    if (gas && miles)
      return Math.round(miles/gas*100)/100
    else
      return 0;
  }

  const submitForm = (e) => {
    e.preventDefault();
    let mpgData;
    if (window.localStorage.getItem('mpgData')) {
      mpgData = JSON.parse(window.localStorage.getItem('mpgData'));
    } else {
      mpgData = [];
    }

    const updatedMpgData = [{
      date,
      mpg: calcMpg(),
    }].concat(mpgData)
    window.localStorage.setItem('mpgData', JSON.stringify(updatedMpgData));
    window.location='/history'
  }

  return (
    <form
      onSubmit={submitForm}>
      <div>
        <span className="mpg">{calcMpg()}</span>MPG
      </div>
      <label>
        Date
        <input
          type="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          />
      </label>
      <label>
        Gas
        <input
          type="number"
          value={gas}
          onChange={(e) => setGas(e.target.value)}
          />
        gal.
      </label>
      <label>
        Miles
        <input
          type="number"
          value={miles}
          onChange={(e) => setMiles(e.target.value)}
          />
        mi.
      </label>
      <button
        type="sumbit">
        Save
      </button>
      <div>
        <Link to="/history">View history</Link>
      </div>
    </form>
  );
}

export default Form;
