import './History.css';

import React from 'react';
import { Link } from 'react-router-dom';

import TableRow from '../components/TableRow.js'
import { dateSorter } from '../helpers/dateStuff.js'

const History = () => {
  let mpgData = [];
  if (window.localStorage.getItem('mpgData')) {
    mpgData = JSON.parse(window.localStorage.getItem('mpgData')).sort(dateSorter)
  }

  return (
    <>
      <h1>Recent MPG data</h1>
      <table>
        <thead>
          <tr>
            <th>Date</th>
            <th>MPG</th>
          </tr>
        </thead>
        <tbody>
          {mpgData.map(({date, mpg}, i) => <TableRow key={i} date={date} mpg={mpg}/>)}
        </tbody>
      </table>
      <Link to="/">Add MPG data</Link>
    </>
  )
}

export default History;
